.title {
  font-family: 'Times New Roman', Times, serif;
  color: #707070;
  font-size: 96px;
  letter-spacing: 15px;
  position: absolute;
  top: 220px;
  left: 250px;
}

.textfield-wrapper {
  margin-top: 600px;
  text-align: center;
}

.textfield {
  border-radius: 70px;
  border: none !important;
  outline: none !important;
  height: 70px;
  width: 40%;
  font-size: 30px;
}

.svg-triangle{
  margin: 0 auto;
  max-width: 40px;
  height: 40px;    
}   

.svg-triangle polygon {
  fill:#edc1c1;
}